.nav{
    margin-top: 10px;
    margin-right:30%;
    margin-left: 30%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 20px;
}
.icon{
    font-size: 20px;
}
.leaf{
    font-size: 40px;
    margin-right: 30px;
}
.listNav{
    font-size: 20px;
}
.text{
    font-size: 15px;
}
.phone{
    margin-left:15px;
    margin-top:-6px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}
.stickyButton{
    position: fixed;
    bottom: 20px; 
    z-index: 99;
    margin-left:400px;
    border-radius: 100%;
    width: 50px;
    height: 50px;
    font-size: 25px;
    color:#303333;
    background:lightgrey;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}
.active{
    font-size: 15px;
    padding: 5px;
    width:120px;
    height: 35px;
    background-color:#8FB6EF;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-collapse: collapse;
}
.nbutton{
    font-size: 15px;
    padding: 5px;
    width: 150px;
    height: 35px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-collapse: collapse;

}
.button{
    font-size: 15px;
    padding: 5px;
    width: 120px;
    height: 35px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-collapse: collapse;
}
.font{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 15px;
}
.list{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 15px;
}
